  setDataFetch = async (data) => {
    const response = await fetch(
      "https://diniz.com.mx/diniz/servicios/services/microS/dashCupones_service.php",
      {
        method: "POST",
        body: data,
        header: new Headers({
          "Content-Type": "application/json",
        }),
      }
    );
    return await response.json();
  };
  data = (e) => {
    let id = "";
    if (!e.target) id = e;
    else id = e.target.id;
    this.setState({ loading: true });
    this.setState({ finish: false });
    this.setState({ consultado: false });

    const data = new FormData();
    data.append("optionService", "SelectData");
    data.append("type", id);
    this.setDataFetch(data).then((response) => {
      if (response.OK) {
        let origin = [
          { data: [], name: "Venta Web" },
          { data: [], name: "Empleados" },
          { data: [], name: "Convenios" },
          { data: [], name: "Total" },
        ];
        response.origen.forEach((element, key) => {
          origin[key].data = element;
        });
        this.setState({ origin: origin });
        let state = [
          { data: [], name: "Redimidos" },
          { data: [], name: "Habilitado" },
          { data: [], name: "Cancelados" },
        ];
        response.state.forEach((element, key) => {
          state[key].data = element;
        });
        console.log('aaaaaa');
        // console.log(state)
        this.setState({ state: state });
        let products = [];
        let label = [];
        response.products.forEach((element, key) => {
          if (element.data > 35)
            products.push({ data: [element.data], name: [element.name] });
          label.push(element.name);
        });
        // console.log(products)
        this.setState({ products: products });
        this.setState({ creados: response.creados });
        this.setState({ redimidos: response.redimidos });
        this.setState({ cancelados: response.cancelados });

        this.setState({ finish: true });
        setTimeout(() => {
          this.setState({ loading: false, finish: false, consultado: true });
        }, 350);
        // this.setState({label:label})
      } else this.setState({ consultado: false });
      // console.log(response);
    });
  };
