import * as Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";
import React, { Component } from "react";
import {} from "reactstrap";
import HighContrastDark from "highcharts/themes/high-contrast-dark";
import $ from "jquery";
import { motion, AnimateSharedLayout, AnimatePresence } from "framer-motion";
HighContrastDark(Highcharts);
class CuadroInformativo extends Component {
  constructor(props) {
    super(props);
    this.chartRef = React.createRef();
    this.state = {
      movido: false,
      animate: { opacity: 1 },
      initial: { opacity: 0 },
      transition: { type: "spring", duration: 5 },
    };
  }
  llamado = (e) => {
    if (!this.state.movido) {
      // $("#grafica" + this.props.id).css('scale' , '.5')
      // $("#grafica" + this.props.id).css('width' , '1000px')
      // $("#grafica" + this.props.id).css('margin' , '100px')

      // $("#grafica" + this.props.id).css('background' , 'red')
      // console.log(      $("#grafica" + this.props.id).position.left)
      // this.chartRef.current.chart.reflow()
      document.getElementById("shadowBox" + this.props.id).style.display =
        "block";
      var target = e.target;
      while (target.className != "bg2 contendorInfo") {
        target = target.parentNode;
      }
      let tar2 = target;
      target = target.getBoundingClientRect();
      let movimientoX = window.innerWidth / 2 - target.left - target.width / 2;
      let movimientoY = window.innerHeight / 2 - target.top - target.height / 2;
      var scrolY = window.scrollY;
      let scale = (window.innerHeight / target.height) * 0.90;
      console.log(tar2);
      window.onscroll = function () {
        window.scrollTo({
          top: scrolY,
          behavior: "instant",
        });
      };
      this.setState({
        animate: {
          scale: scale,
          opacity: 1,
          zIndex: 100,
          x: movimientoX,
          y: movimientoY,
        },
        transition: { duration: 0.8 },
        movido: true,
      });
    } else if (e.target.id === "shadowBox" + this.props.id) {
      document.getElementById("shadowBox" + this.props.id).style.display =
        "none";
      window.onscroll = null;
      this.setState({
        animate: { scale: 1, opacity: 1, zIndex: 100, x: 0, y: 0 },
        transition: { duration: 1 },
        movido: false,
      });
      setTimeout(() => {
        this.setState({
          animate: { zIndex: 1, opacity: 1 },
          transition: { duration: 0.2 },
        });
      }, 1000);
    }
  };
  render() {
    return (
      <>
        <div
          onClick={!this.props.type ? this.llamado : () => {}}
          className={"shadowBox"}
          id={"shadowBox" + this.props.id}
          style={{ width: "100vw", height: "100vh", }}
        ></div>
        <motion.div
          initial={this.state.initial}
          animate={this.state.animate}
          transition={this.state.transition}
        >
          <div
            style={{width: this.props.type === 3 ? "auto" : 750, borderRadius:10,}}
            className="bg2 contendorInfo"
            id="contendorInfo"
            onClick={!this.props.type ? this.llamado : () => {}}
          >
            {this.props.title && !this.props.data ? (
              <h2 className="titleInfo">{this.props.title}</h2>
            ) : (
              ""
            )}
            <div
              className={this.props.type === 3 ? "d-flex flex-wrap" : ""}
              id={"grafica" + this.props.id}
            >
              {!this.props.type ? (
                <HighchartsReact
                  ref={this.chartRef}
                  id="highcharts"
                  highcharts={Highcharts}
                  options={{
                    chart: {
                      type: this.props.typeGraph
                        ? this.props.typeGraph
                        : "spline",
                      events: {},
                    },
                    title: {
                      text: this.props.title,
                    },
                    xAxis: {
                      categories: this.props.label,
                    },
                    series: this.props.data,
                    plotOptions: {
                      spline: {
                        dataLabels: {
                          enabled: true,
                        },
                        enableMouseTracking: false,
                      },
                      series: {
                        dataLabels: {
                          enabled: true,
                        },
                        point: {
                          events: {
                            // mouseOver: this.setHoverData.bind(this),
                          },
                        },
                      },
                    },
                  }}
                />
              ) : (
                this.props.children
              )}
            </div>
          </div>
        </motion.div>
      </>
    );
  }
}
export default CuadroInformativo;
